.sidebar {
    z-index: 1;
    padding: 0;
    position: fixed;
    display: flex;
    flex-direction: column;
    background-color: #404040;
    max-width: 200px;
    padding: 0;
    height: calc(100% - 56px);

    .divider {
        width: 100%;
        min-height: 0.99px;
        background-color: #303030;
    }

    .item {
        cursor: pointer;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        margin: 0;
        padding: 0;
        width: 100%;
        height: auto;
        padding: 12px 0;
        box-sizing: border-box;
        transition: background-color 0.25s ease;

        .profile-icon {
            width: 32px;
            height: 32px;
            border-radius: 50%;
            box-shadow: 0 1px 2px 0.5px #30303050;
        }

        .item-icon {
            margin: 0 12px;
            min-width: 32px;
        }

        .item-name {
            width: 100%;
        }

        .profile-name {
            font-weight: bold;
            font-size: 14px;
        }

        &:hover {
            background-color: #303030;
        }

        &.active {
            background-color: #303030;
        }
    }

    &.mobile {
        transition: left 0.25s linear;
        left: -100%;
    }

    &.mobile.active {
        left: 0;
    }

    @media only screen and (max-width: 768px) {
        height: 100%;
        max-width: 250px;
    }
}

.mob-sidebar-wrapper {
    width: 100%;
    height: 100%;
    background-color: #30303030;
    position: absolute;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.25s ease;

    &.active {
        opacity: 1;
        pointer-events: all;
    }
}
